$font-family-base: "Catamaran";
$primary: #d520a2;
$body-bg: #f6f6f6;
$body-color: #111;
$link-color: #aa9300;

// Import all of Bootstrap's CSS
@import "../../node_modules/bootstrap/scss/bootstrap";
// Default variable overrides
@font-face {
    font-family: "Catamaran";
    src: url("../fonts/Catamaran/Catamaran-VariableFont_wght.ttf");
}
@font-face {
    font-family: "libre";
    src: url("../fonts/Libre_Barcode/LibreBarcode128Text-Regular.ttf");
}
.calloutText{
    font-family: 'libre';
}
html, body {
    font-family: "Catamaran", sans-serif; /*Specify your font name here*/
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

.awesome {
    font-family: 'libre';
    width: 100%;
    color: #313131;
    font-size: 85px;
  
    animation: colorchange 20s infinite alternate; /* Standard property */
    -webkit-animation: colorchange 20s infinite alternate; /* WebKit prefix */
    -moz-animation: colorchange 20s infinite alternate; /* Mozilla prefix */
    -o-animation: colorchange 20s infinite alternate; /* Opera prefix */
    -ms-animation: colorchange 20s infinite alternate; /* Microsoft prefix */
  }
  
  @keyframes colorchange {
    0% {
      color: blue;
    }
    10% {
      color: #8e44ad;
    }
    20% {
      color: #1abc9c;
    }
    30% {
      color: #d35400;
    }
    40% {
      color: blue;
    }
    50% {
      color: #34495e;
    }
    60% {
      color: blue;
    }
    70% {
      color: #2980b9;
    }
    80% {
      color: #f1c40f;
    }
    90% {
      color: #2980b9;
    }
    100% {
      color: pink;
    }
  }
  
  @-webkit-keyframes colorchange {
    0% {
      color: blue;
    }
    10% {
      color: #8e44ad;
    }
    20% {
      color: #1abc9c;
    }
    30% {
      color: #d35400;
    }
    40% {
      color: blue;
    }
    50% {
      color: #34495e;
    }
    60% {
      color: blue;
    }
    70% {
      color: #2980b9;
    }
    80% {
      color: #f1c40f;
    }
    90% {
      color: #2980b9;
    }
    100% {
      color: pink;
    }
  }
  
