.imageProjects{
    max-height: 400px;
}


.circle {
    border-radius: 50%;
    position: absolute;
    transform: translateY(-50%);
    z-index: -1; /* Place circles behind the image */
  }

.circle-left {
    width: 140px;
    height: 140px;
    left: 12px; /* Adjust the left offset for the left circle */
    top: 49%;
    background-color: #f06ccb81; /* Circle background color */
  }

.circle-right {
    width: 120px;
    height: 120px;
    right: -40px; /* Adjust the right offset for the right circle */
    top: 40%;
    background-color: #69ec64b0; /* Circle background color */
    z-index: -2;
  }